* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.raindrop {
  position: absolute;
  background-color: rgba(173, 216, 230, 0.7);
  border-radius: 50%;
  animation: fall linear infinite;
  transform: translateY(0);
}

@keyframes fall {
  0% {
    transform: translateY(-1000px);
  }
  100% {
    transform: translateY(100vh);
  }
}

.cloud {
  position: absolute;
  bottom: 90%;
  background-color: #ffffff;
  border-radius: 50%;
  opacity: 0.8;
  animation: moveClouds linear infinite;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

@keyframes moveClouds {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ee98b7;
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.Box {
  width: 750px;
  height: 1000px;
  background-color: #413d3f;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
  max-width: 100%;
  max-height: 100%;
}

.Box img {
  height: 750px;
  width: 750px;
  position: relative;
  margin-bottom: 500px;
}


.Box h1 {
  font-size: 2.5rem;
  font-family: 'Arial', sans-serif;
  font-weight: bold;
  color: #fff;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  margin: 10px 0;
  padding: 20px;
  text-align: center;
  max-width: 80%;
  margin-top: 350px;
}

.Box button {
  padding: 16px 32px;
  font-size: 32px;
  border-radius: 25px;
  border: none;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.ButtonContainer {
  display: flex;
  justify-content: space-between;
  width: 50%;
  position: absolute;
  bottom: 25px;
  padding: 0 20px;
}

.ButtonContainer button {
  flex: 0 0 auto;
}

.Yes {
  background: linear-gradient(145deg, #30e080, #2dbf6b);
  color: white;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

.No {
  background: linear-gradient(145deg, #ff4d4d, #e62424);
  color: white;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

.Yes:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.No:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.Yes:active,
.No:active {
  transform: translateY(1px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}
